@import url("https://fonts.googleapis.com/css?family=Jolly+Lodger");

:root {
  --venom: "Jolly Lodger", sans-serif;
}
* {
  box-sizing: border-box;
}
body {
  background: rgb(26, 26, 26);
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  font-family: var(--venom);
}

h1 {
  color: white;
  font-size: 3em;
}
.venom {
  position: relative;
  width: 500px;
  height: 800px;
  &-left-eye {
    position: absolute;
    top: 60px;
    left: 5%;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    background: white;
    margin: 1em;
    &:before {
      content: "";
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translateX(-81%) translateY(-41%) rotateZ(34deg);
      width: 210px;
      height: 120px;
      border-bottom: 180px solid white;
      border-radius: 0 0 0 100%;
      transform-origin: right;
    }
  }
  &-right-eye {
    position: absolute;
    top: 60px;
    right: 5%;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    background: white;
    margin: 1em;
    &:before {
      content: "";
      position: absolute;
      top: 31%;
      left: 50%;
      transform: translateX(-115%) translateY(-56%) rotateY(180deg)
        rotateZ(34deg);
      width: 210px;
      height: 120px;
      border-bottom: 180px solid white;
      border-radius: 0 0 0 100%;
      transform-origin: right;
    }
  }
  &-mouth {
    position: absolute;
    top: 430px;
    left: 50%;
    width: 0px;
    height: 1px;
    background: white;
    transform: translateX(-50%) translateY(-50%);
    border-radius: 0 0 80% 80%;
    border-bottom: 219px solid red;
    &:before {
      content: "";
      position: absolute;
      top: 0%;
      left: 30%;
      transform: translateX(-99%) translateY(-50%);
      width: 245px;
      height: 220px;
      border-bottom: 280px solid rgb(155, 32, 32);
      border-radius: 0 0 0 80%;
      transform-origin: right;
    }
    &:after {
      content: "";
      position: absolute;
      top: 31%;
      left: 60%;
      transform: translateX(-100%) translateY(-50%) rotateY(180deg);
      width: 245px;
      height: 220px;
      border-bottom: 280px solid rgb(155, 32, 32);
      border-radius: 0 0 0 80%;
      transform-origin: right;
      z-index: -1;
    }
    &-bottom {
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translateX(-50%) translateY(66%);
      width: 300px;
      height: 1px;
      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 145%;
        transform: translateX(-62%) translateY(-33%) rotate(24deg);
        width: 200px;
        height: 500px;
        border-radius: 50%;
        background: rgb(26, 26, 26);
        z-index: 1;
      }
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 45%;
        transform: translateX(-170%) translateY(-33%) rotate(-24deg);
        width: 200px;
        height: 500px;
        border-radius: 50%;
        background: rgb(26, 26, 26);
        z-index: 1;
      }
    }
  }
  &-tongue {
    position: absolute;
    top: 380px;
    left: 50%;
    transform: translateX(-50%) translateY(-10%);
    width: 130px;
    height: 90px;
    border-bottom: 150px solid rgba(255, 45, 45, 0.8);
    border-radius: 0 0 0 100%;
    transform-origin: right;
    &-inside {
      position: absolute;
      top: 385px;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 200px;
      height: 200px;
      background: rgb(26, 26, 26);
      filter: blur(40px);
    }
  }
  &-teeth {
    position: absolute;
    top: 209px;
    width: 100%;
    height: 300px;
  }
  &-tooth {
    &:nth-child(1) {
      position: absolute;
      top: 40%;
      left: 25%;
      transform: translateX(-50%) translateY(-50%);
      width: 50px;
      height: 100px;
      background: white;
      clip-path: polygon(
        -1px 37px,
        8px 12px,
